.navigation {
  position: fixed;
  margin: 0 1rem;
  right: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 99;
}

.nav-element {
  display: block;
  margin: 0.4rem 0;
  font-size: 0.9rem;
}

.nav-element>div {
  border: 2px solid #666;
  border-radius: 50%;
  width: 0.5rem;
  height: 0.5rem;
}

.nav-text {
  color: transparent;
  position: relative;
  top: -0.4rem;
  left: -3.2rem;
}

.nav-element>div:hover, .nav-element>div.active {
  background-color: #666;
}

.nav-element>div:hover .nav-text {
  color: #666;
  -webkit-transition: 0.2s ease-in;
  -o-transition: 0.2s ease-in;
  transition: 0.2s ease-in;
  left: -4rem;
}



